body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(40, 5, 5);
  background: linear-gradient(
    90deg,
    rgba(40, 5, 5, 0.6561975131849616) 0%,
    rgba(200, 132, 64, 0.434908997778799) 8%,
    rgba(115, 124, 80, 1) 100%
  );
}
.search-tool .search-bar {
  display: block;
  width: 20%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgb(194, 164, 109, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25 px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s case;
}

.search-tool .search-bar:focus {
  background-color: rgb(194, 164, 109, 0.75);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
